.filter-type-title {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: .5rem 0;
  font-size: 14px;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

.dropdown-toggle {
  border-radius: 4px;
  width: 100px;
  height: 32px;
  display: flex;
  justify-content: space-between;
  padding: 5px;
  align-items: center;
  background: none;
  outline: none;
  border: 1px solid #eee;
  margin-right: 10px;
}

.filter-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.filter-header span{
  color: #00B16E;
  cursor: pointer;
}

.category-filter .active{
background-color: red;
border-radius: 160px;
padding: 8px;
/* color: #fff !important; */
font-weight: 800;
background-color: #E1E4E8;
}

.custom-checkbox .custom-control-input:checked~.custom-control-label::before{
  background-color: #00BD76;
}