@import url(https://fonts.googleapis.com/css2?family=Khula:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

footer {
  background-color: #fafbfb;
}

.form-control {
  padding-left: 2.5rem;
}

nav {
  box-shadow: 0px 1px 10px #999;
}

.form-control-feedback {
  position: absolute;
  z-index: 2;
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 2.375rem;
  left: 0;
  text-align: center;
  pointer-events: none;
  color: #aaa;
}

footer .text-muted {
  font-size: 12px;
  font-weight: 600;
}

@media (max-width: 576px) {
  .sideFilter {
    display: none;
  }
  .categoryFilter {
    display: none;
  }
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Roboto', sans-serif;
}

a {
    text-decoration: none;
}

.auction-card {
    width: 100%;
    border-radius: 8px;
    position: relative;
    box-shadow: 0 2px 7px #dfdfdf;
    margin: auto;
    /* background: #fafafa; */
    margin-bottom: 10px;
}

.badgeOver-cardImage {
    position: absolute;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* top: 20px; */
    width: 100%;
}

.badge {
    text-transform: uppercase;
    color: #0081B5;
    font-family: Khula;
    font-size: 10px;
    letter-spacing: 1.5px;
    line-height: 12px;
    text-align: center;
    padding: 3px 10px;
    background-color: rgb(176, 224, 243);
    border-radius: 160px;
}

.view-badge {
    color: #fff;
    background-color: #283A5B;
    font-family: Khula;
    font-size: 10px;
    font-weight: 800;
    letter-spacing: 1.5px;
    line-height: 12px;
    text-align: center;
    padding: 3px 10px;
    border-radius: 160px;
}

.auctions-tag {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 0;
}

.auctions-tag .status {
    color: red;
    /* padding: 5px; */
    margin:0 5px;
    border-radius: 100%;
    
}

.auction-catagory {
    text-transform: uppercase;
    color: #00B16E;
    font-family: Khula;
    font-size: 10px;
    font-weight: 600;
    letter-spacing: 1.2px;
    line-height: 12px;
    text-align: left;
    border-radius: 4px;
    background-color: #ADDFC6;
    padding: 4px 8px;
}

.auction-tumb {
    display: flex;
    align-items: center;
    justify-content: center;
    /* padding: 50px; */
    background: #f0f0f0;
}

.auction-tumb img {
    max-width: 100%;
    max-height: 100%;
}

.auction-details {
    padding: 16px;
}

.auction-details h4 {
    padding: 8px 0;
    margin-bottom: 0;
}

.amount-details {
    padding: 16px 0px;
}

.amount-details .amount {
    color: #0A1F44;
    font-family: Khula;
    font-size: 18px;
    font-weight: 600;
    line-height: 19px;
    text-align: left;
}

.amount-details .amount-title {
    color: #8A94A6;
    font-family: Khula;
    font-size: 14px;
    font-weight: 500;
    line-height: 14px;
    text-align: left;
}

.auction-details h4 a {
    font-size: 14px;
    font-weight: 600;
    line-height: 17px;
    display: block;
    text-transform: uppercase;
    color: #0A1F44;
    font-family: Khula;
    text-decoration: none;
}

.auction-details p {
    font-size: 15px;
    line-height: 22px;
    margin-bottom: 18px;
    color: #999;
}

.seprator {
    overflow: hidden;
    border-top: 1px solid #eee;
}

.action-buttons {
    display: flex;
    justify-content: space-between;
}

.action-buttons button {
    width: 100%;
    font-family: Khula;
    font-size: 14px;
    font-weight: 800;
    padding: 8px;
    text-align: center;
    border-radius: 4px;
}

.action-buttons .btn-watch {
    background-color: #fff;
    box-shadow: 0 2px 7px #dfdfdf;
    font-weight: 600;
}

.btn-watch i {
    color: orange;
}
.filter-type-title {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: .5rem 0;
  font-size: 14px;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

.dropdown-toggle {
  border-radius: 4px;
  width: 100px;
  height: 32px;
  display: flex;
  justify-content: space-between;
  padding: 5px;
  align-items: center;
  background: none;
  outline: none;
  border: 1px solid #eee;
  margin-right: 10px;
}

.filter-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.filter-header span{
  color: #00B16E;
  cursor: pointer;
}

.category-filter .active{
background-color: red;
border-radius: 160px;
padding: 8px;
/* color: #fff !important; */
font-weight: 800;
background-color: #E1E4E8;
}

.custom-checkbox .custom-control-input:checked~.custom-control-label::before{
  background-color: #00BD76;
}
