.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

footer {
  background-color: #fafbfb;
}

.form-control {
  padding-left: 2.5rem;
}

nav {
  box-shadow: 0px 1px 10px #999;
}

.form-control-feedback {
  position: absolute;
  z-index: 2;
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 2.375rem;
  left: 0;
  text-align: center;
  pointer-events: none;
  color: #aaa;
}

footer .text-muted {
  font-size: 12px;
  font-weight: 600;
}

@media (max-width: 576px) {
  .sideFilter {
    display: none;
  }
  .categoryFilter {
    display: none;
  }
}